/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Box, Container, Grid, Typography } from '@material-ui/core';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Image from 'gatsby-image';
import { useMemo } from 'react';
import { Colors } from 'utils/colors';

const Footer = () => {
  const data = useStaticQuery(graphql`
    query FooterQuery {
      logo: file(absolutePath: { regex: "/logo-text.png/" }) {
        childImageSharp {
          fixed(width: 100, height: 24) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
          }
        }
      }
    }
  `);
  const {
    logo: {
      childImageSharp: { fixed: logoUrl },
    },
    site: {
      siteMetadata: { author },
    },
  } = data;
  const thisYear = useMemo(() => new Date().getFullYear(), []);

  return (
    <Box
      component="footer"
      css={css`
        background-color: ${Colors.White};
        padding: 2rem 2rem 5rem 2rem;
      `}
    >
      <Container>
        <Grid container spacing={0} alignItems="center">
          <Grid>
            <Link
              to="/"
              css={css`
                display: block;
                margin-bottom: 0.2rem;
              `}
            >
              <Image fixed={logoUrl} alt="" />
            </Link>
            <Typography variant="caption" component="p" color="textSecondary">
               {thisYear} {author.name} Powered by <GatsbyLink href="https://www.gatsbyjs.org/">Gatsby</GatsbyLink>
            </Typography>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

const GatsbyLink = styled.a`
  color: ${Colors.Primary};
  text-decoration: none;
`;

export default Footer;
