/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { ReactNode, useMemo } from 'react';
import Header from 'components/Header';
import Footer from 'components/Footer';
import { Container, Grid, useTheme, makeStyles, Theme } from '@material-ui/core';
import { Colors } from 'utils/colors';

interface Props {
  location: Location;
  children: ReactNode;
  left?: ReactNode;
  right?: ReactNode;
  rightSticky?: boolean;
  leftSticky?: boolean;
}

const stickyStyle = css`
  align-self: flex-start;
  position: sticky;
  top: 1rem;
`;

const Layout = ({ children, left, right, leftSticky = false, rightSticky = false }: Props) => {
  const theme = useTheme();
  const createOrderStyle = (position: 'left' | 'right' | 'center', theme: Theme) =>
    makeStyles({
      root: {
        [theme.breakpoints.down('md')]: {
          order: position === 'left' ? 2 : position === 'right' ? 3 : position === 'center' ? 1 : 0,
        },
      },
    })();

  const leftOrderStyle = createOrderStyle('left', theme);
  const centerOrderStyle = createOrderStyle('center', theme);
  const rightOrderStyle = createOrderStyle('right', theme);

  const desktopCenterGridSize = useMemo(() => {
    if (left && right) {
      return 6;
    } else if (left || right) {
      return 8;
    } else {
      return 12;
    }
  }, [left, right]);
  return (
    <div
      css={css`
        background-color: ${Colors.Grey['100']};
      `}
    >
      <Header />
      <Container
        component="main"
        css={css`
          padding: 1.5rem 1rem;
          ${theme.breakpoints.up('lg')} {
            padding: 3rem 2rem;
          }
        `}
      >
        <Grid container spacing={2}>
          {left && (
            <Grid item xs={12} lg={right ? 3 : 4} css={leftSticky && stickyStyle} className={leftOrderStyle.root}>
              {left}
            </Grid>
          )}
          <Grid item xs={12} lg={desktopCenterGridSize} className={centerOrderStyle.root}>
            {children}
          </Grid>
          {right && (
            <Grid item xs={12} lg={left ? 3 : 4} css={rightSticky && stickyStyle} className={rightOrderStyle.root}>
              {right}
            </Grid>
          )}
        </Grid>
      </Container>
      <Footer />
    </div>
  );
};

export default Layout;
