/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Box, Container, Grid, useTheme, makeStyles } from '@material-ui/core';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Image from 'gatsby-image';
import { GlobalMenus } from 'models/menu';
import { rhythm } from 'utils/typography';
import { Colors } from 'utils/colors';
import { useCallback } from 'react';
import { linkEvent } from 'utils/loggers';

const Header = () => {
  const theme = useTheme();
  const logoGridStyle = makeStyles({
    root: {
      marginBottom: '1rem',
      [theme.breakpoints.up('lg')]: {
        marginBottom: 0,
        marginTop: 4,
        marginRight: '1.5rem',
      },
    },
  })();

  const data = useStaticQuery(graphql`
    query HeaderQuery {
      logo: file(absolutePath: { regex: "/logo-text.png/" }) {
        childImageSharp {
          fixed(width: 100, height: 24) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `);
  const {
    logo: {
      childImageSharp: { fixed: logoUrl },
    },
  } = data;

  const handleLogoClick = useCallback(() => {
    linkEvent('/', '   ');
  }, []);

  const handleMenuClick = useCallback((url: string) => {
    linkEvent(url, '   ');
  }, []);

  return (
    <Box
      component="header"
      boxShadow={3}
      css={css`
        padding: 1rem;
        background-color: ${Colors.White};
      `}
    >
      <Container>
        <Grid container spacing={0} alignItems="center">
          <Grid item xs={12} lg={'auto'} className={logoGridStyle.root}>
            <Link to="/" onClick={handleLogoClick}>
              <Image fixed={logoUrl} alt="" />
            </Link>
          </Grid>
          <Grid item>
            <Box
              component="ul"
              display="flex"
              css={css`
                margin: 0;
                padding: 0;
              `}
            >
              {GlobalMenus.map(menu => (
                <Box
                  key={menu.path}
                  component="li"
                  css={css`
                    list-style: none;
                    margin-right: 1.2rem;
                    margin-bottom: 0;
                    font-size: ${rhythm(0.8)};
                  `}
                >
                  <MenuLink
                    activeClassName="active"
                    to={menu.path}
                    css={css`
                      text-decoration: none;
                    `}
                    onClick={() => handleMenuClick(menu.path)}
                  >
                    {menu.name}
                  </MenuLink>
                </Box>
              ))}
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

const MenuLink = styled(Link)`
  text-decoration: none;
  color: inherit;
  &.active {
    color: ${Colors.Primary} !important;
  }
  &:visited {
    color: inherit;
  }
`;

export default Header;
